import * as React from 'react'
import * as ReactDOM from 'react-dom'
import { ipcRenderer } from "electron";
// 
// antd
// import App from "./antd/App";
// sematic-ui-react
import App from "./sematic-ui-react/App";




/**
 * 
 */
document.getElementById('root').addEventListener("mouseup", (e) => {
    console.log('[DEBG]', e.which);
    let bag = {
        type: 'MOUSE_CLICK',
        from: 'r',
        to: 'b',
        value: e.which
    }
    send(bag)
})

/**
 * m
 */
ipcRenderer.on('bag', (event: any, data: string) => {
    var raw = data.toString()
    // mainbag
    var piece = raw.split('}{')
    for (let i = 0; i < piece.length; i++) {
        if (i != piece.length - 1) {
            piece[i] += '}'
        }
        if (i != 0) {
            piece[i] = '{' + data[i]
        }
    }
    for (let i = 0; i < piece.length; i++) {
        // console.log('[DEBG]', piece[i]); // 
        let bag: any = JSON.parse(piece[i])
        parseBag(bag)
    }
    // mainbag
    // console.log('[DEBG]', raw); // 
    // var bag: any = JSON.parse(raw)
    // parseBag(bag)
})

/**
 * m
 * @param bag 
 */
function send(bag: any) {
    console.log('[DEBG]', JSON.stringify(bag)); // 
    ipcRenderer.send('bag', JSON.stringify(bag))
}

/**
 * Bag
 * @param bag 
 */
function parseBag(bag: any) {
    if (bag.type == 'connected') {
        console.log('[DEBG]')
        app.isConnected = true
        update()
    } else if (bag.type == 'loaded') {
        console.log('[DONE]')
        app.isLoaded = true
        update()
    } else if (bag.type == 'title') {
        document.title = bag.value
    } else if (bag.type == 't') { // 
        if (app.pages.length == 0) { // tPage
            app.pages.push({ type: 'page', children: [] })
        }
        let iPage = app.pages.length - 1 // Pageindex
        if (bag.value == '') { // t
            app.pages[iPage].children.push({ type: 'line', children: [] })
        } else {
            if (app.pages[iPage].children.length == 0) { // tLine
                app.pages[iPage].children.push({ type: 'line', children: [] })
            }
            // pagelinet
            let iLine = app.pages[iPage].children.length - 1 // Lineindex
            app.pages[iPage].children[iLine].children.push(bag)
        }
        update()
    } else if (bag.type == 'b') { // 
        if (app.pages.length == 0) { // bPage
            app.pages.push({ type: 'page', children: [] })
        }
        let iPage = app.pages.length - 1 // Pageindex
        if (app.pages[iPage].children.length == 0) { // bLine
            app.pages[iPage].children.push({ type: 'line', children: [] })
        }
        // pagelineb
        let iLine = app.pages[iPage].children.length - 1 // Lineindex
        bag.value.func = send // 
        app.pages[iPage].children[iLine].children.push(bag)
        update()
    } else if (bag.type == 'h') { // 
        if (app.pages.length == 0) { // hPage
            app.pages.push({ type: 'page', children: [] })
        }
        let iPage = app.pages.length - 1 // Pageindex
        if (app.pages[iPage].children.length == 0) { // hLine
            app.pages[iPage].children.push({ type: 'line', children: [] })
        }
        // pagelineh
        let iLine = app.pages[iPage].children.length - 1 // Lineindex
        app.pages[iPage].children[iLine].children.push(bag)
        update()
    } else if (bag.type == 'progress') { // 
        if (app.pages.length == 0) { // progressPage
            app.pages.push({ type: 'page', children: [] })
        }
        let iPage = app.pages.length - 1 // Pageindex
        if (app.pages[iPage].children.length == 0) { // progressLine
            app.pages[iPage].children.push({ type: 'line', children: [] })
        }
        // pagelineprogress
        let iLine = app.pages[iPage].children.length - 1 // Lineindex
        app.pages[iPage].children[iLine].children.push(bag)
        update()
    } else if (bag.type == 'rate') { // 
        if (app.pages.length == 0) { // ratePage
            app.pages.push({ type: 'page', children: [] })
        }
        let iPage = app.pages.length - 1 // Pageindex
        if (app.pages[iPage].children.length == 0) { // rateLine
            app.pages[iPage].children.push({ type: 'line', children: [] })
        }
        // pagelineb
        let iLine = app.pages[iPage].children.length - 1 // Lineindex
        bag.value.func = send // 
        app.pages[iPage].children[iLine].children.push(bag)
        update()
    } else if (bag.type == 'radio') { // 
        if (app.pages.length == 0) { // radioPage
            app.pages.push({ type: 'page', children: [] })
        }
        let iPage = app.pages.length - 1 // Pageindex
        if (app.pages[iPage].children.length == 0) { // radioLine
            app.pages[iPage].children.push({ type: 'line', children: [] })
        }
        // pagelineb
        let iLine = app.pages[iPage].children.length - 1 // Lineindex
        bag.value.func = send // 
        app.pages[iPage].children[iLine].children.push(bag)
        update()
    } else if (bag.type == 'input') { // 
        if (app.pages.length == 0) { // inputPage
            app.pages.push({ type: 'page', children: [] })
        }
        let iPage = app.pages.length - 1 // Pageindex
        if (app.pages[iPage].children.length == 0) { // inputLine
            app.pages[iPage].children.push({ type: 'line', children: [] })
        }
        // pagelineb
        let iLine = app.pages[iPage].children.length - 1 // Lineindex
        bag.value.func = send // 
        app.pages[iPage].children[iLine].children.push(bag)
        update()
    } else if (bag.type == 'divider') { // 
        if (app.pages.length == 0) { // dividerPage
            app.pages.push({ type: 'page', children: [] })
        }
        let iPage = app.pages.length - 1 // Pageindex
        if (app.pages[iPage].children.length == 0) { // dividerLine
            app.pages[iPage].children.push({ type: 'line', children: [] })
        }
        // pagelineb
        let iLine = app.pages[iPage].children.length - 1 // Lineindex
        app.pages[iPage].children[iLine].children.push(bag)
        update()
    } else if (bag.type == 'page') { // 
        app.pages.push({ type: 'page', children: [] })
        for (let i = 0; i < app.pages.length - 20; i++) { // 50
            app.pages.splice(0, 1)
        }
        update()
    } else if (bag.type == 'mode') { // 
        app.mode = bag.value
        update()
    } else if (bag.type == 'clear') { // 
        if (bag.value['last']) {
            app.pages.pop()
        } else {
            app.pages = []
        }
        update()
    }
}
let tmp: any[] = []
let app = {
    isConnected: false,
    isLoaded: false,
    pages: tmp,
    mode: tmp
}
update()
function update() {
    ReactDOM.render(
        <App data={app} />,
        document.getElementById('root')
    )
}